@use "base/variables" as v;
@use "base/mixins" as m;

.navegacion {
  display: flex;
  flex-direction: column;
  @include m.desktop {
    flex-direction: row;
  }

  &__enlace {
    color: v.$blanco;
    font-size: 1.8rem;
    padding: 1.5rem 2.5rem;
    font-weight: v.$black;

    text-align: center;
    &--actual,
    &:hover {
      background-color: v.$blanco;
      color: v.$azul;
    }
  }
}
