@use "base/variables" as v;
@use "base/mixins" as m;

.barra {
  background-color: v.$azul;
  &__contenido {
    @include m.contenedor;
    display: flex;
    flex-direction: column;
    @include m.desktop {
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }
  }
  &__logo {
    @include m.logo;
    @include m.texto-fluido(1rem, 2.5rem);
    h2 {
      margin: 0;
      font-size: 2.5rem;
    }
  }
}
